nav {
    width: 100%;
    font-size: 30px;
    color: #fff;
    margin-bottom: 30px;
    background-color: rgb(56, 56, 56);
    height: auto;
    font-family: 'Bungee', cursive;
    /* position: relative; */
  }
  
  a {
    color: #9d31b3;
  }
  
  a:hover {
    color: #8b4cc7;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 20px 0;
    width: 25%;
   
  }
  
  ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
  }
  
@media only screen and (max-width: 750px) {
  ul {
    font-size: 1.5rem;
  } 
}



